@import '~handsontable/dist/handsontable.css';
@import '~scss/variables';
@import '~scss/custom-properties';

.itooltip {
  display: inline-block;
  position: absolute;
  top: -2px;
  left: 50%;
  right: auto;
  transform: translate3d(-50%, -100%, 0);
  z-index: 150;
  white-space: normal;
  transition: opacity 0.1s;

  font-size: 12px;
  font-weight: normal;
  line-height: 1.1;
  background-color: var(--danger-color);
  color: #fff;
  box-shadow: -1px 1px 3px rgba(0,0,0, 0.3);
  border: 1px solid #fff;
  border-radius: 3px;
  padding: 5px;
  text-align: left;

  // Triangle
  &::after, &::before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -7px;
    width: 0;
    height: 0;
    margin: 0 0 0 -6px;
    border: 1px solid transparent;
    pointer-events: none;
  }
  &::after {
    border-width: 6px 6px 0 6px;
    border-top-color: var(--danger-color);
    bottom: -6px;
    margin-left: -5px;
  }
  &::before {
    border-width: 7px 7px 0 7px;
    border-top-color: #fff;
  }

  // Green tooltip
  &.ok {
    background: var(--brand-color);
    &::after {
      border-top-color: var(--brand-color);
    }
  }
  &:hover {
    z-index: 151;
  }

  .ff-control.ff-control {
    color: #fff;

    option {
      color: $text-color;
    }
  }
}


#htImport {
  // Body tooltip (validation)
  .ht-body-tooltip {
    min-width: 132px;
  }

  // Extra overlay for row headers
  .ht-row-extra {
    position: absolute;

    ff-icon {
      position: absolute;
      cursor: pointer;
      top: 0;
      right: 1px;
      min-width: 14px;
      text-align: center;
      line-height: 22px;
      transition: all 0.1s;

      &:hover {
        transform: scale(1.5);
      }

      &.sense-header, &.sense-ignore, &.sense-check {
        color: var(--text-color);
      }
      &.sense-refresh, &.sense-plus {
        color: var(--brand-color);
      }
      &.sense-error, &.sense-warning {
        color: var(--danger-color);
      }

      &.rotate {
        pointer-events: none;
        animation: rotate 1s infinite linear;
        @keyframes rotate {
          100% {
            transform: rotate(360deg);
          }
        }
      }

      &.ng-animate { // prevent ngIf from waiting for animation and delaying icons
        animation: none 0s;
        transition: 0s;
      }
    }
  }
}

/**
 * Entire import page
 */
#setupImport {
  import-step-init {
    .ff-label {
      display: block;
      padding-top: .5rem;
      margin-bottom: 1rem;

      span, ff-icon {
        vertical-align: super;
        margin-right: 1rem;
      }

      ff-icon {
        margin-right: .8rem;
      }
    }
  }

  .import-dashboard > span {
    flex: auto;
  }
}

.import-dashboard {
  align-items: center;
}

.ht-panel {
  max-width: 100%;

  .ff-dimmer.ff-dimmer {
    z-index: 1000;
  }
}


.import-stats {
  display: flex;
  justify-content: space-between;
  padding-top: 6px;
  line-height: 1;

  & > div {
    display: flex;
  }

  .counter {
    font-size: 26px;
    font-weight: bold;
  }

  ff-icon {
    position: relative;
    top: 0;
    left: 2px;
    font-size: 12px;
  }

  .text {
    position: relative;
    bottom: -14px;
    left: -7px;
    font-size: 9px;
    font-weight: bold;
    text-transform: uppercase;
  }

  .text-danger {
    .text {
      left: -9px;
    }
  }
}
